<template>
  <div class="container">
    <h1>註冊帳號</h1>
    <form class="row g-3 needs-validation" novalidate>
      <div class="col-md-12">
        <label for="validationCustom03" class="form-label">帳號</label>
        <!-- v-mudel為 input限定用法 -->
        <input v-model="account" type="text" class="form-control" id="validationCustom03" required />
        <div class="invalid-feedback">請輸入帳號</div>
      </div>
      <div class="col-md-12">
        <label for="validationCustom03" class="form-label">信箱</label>
        <!-- v-mudel為 input限定用法 -->
        <input v-model="email" type="email" class="form-control" id="validationCustom03" pattern=".+@gmail\.com"
          required />
        <div class="invalid-feedback">請輸入正確的信箱格式</div>
      </div>
      <div class="col-md-12">
        <label for="validationCustom03" class="form-label">密碼</label>
        <input v-model="password" type="password" class="form-control" id="validationCustom03" required />
        <div class="invalid-feedback">請輸入密碼</div>
      </div>

      <div class="col-12">
        <button @click="login" class="btn btn-primary" type="submit">
          註冊
        </button>
      </div>
    </form>
  </div>

  <div v-if="loginSuccess == true" class="col-12 mt-5">註冊成功</div>
</template>
<script>
import axios from 'axios'

export default {
  data() {
    return {
      loginSuccess: false,
      account: '',
      password: '',
      email: '',
    }
  },
  methods: {
    login() {
      //殘疾
      // if (this.account != "") {
      //   if (this.password != "") {
      //     this.loginSuccess = true;
      //   }
      //微正常
      if (this.account != '' && this.password != '' && this.email === true) {
        this.loginSuccess = true
        alert('註冊成功')
      }
    },
  },

  mounted() {
    // Example starter JavaScript for disabling form submissions if there are invalid fields
    ; (() => {
      'use strict'

      // Fetch all the forms we want to apply custom Bootstrap validation styles to
      const forms = document.querySelectorAll('.needs-validation')

      // Loop over them and prevent submission
      Array.from(forms).forEach((form) => {
        form.addEventListener(
          'submit',
          async (event) => {
            if (!form.checkValidity()) {
              event.preventDefault()
              event.stopPropagation()
            } else {
              //因php只認得FormData的實力對象
              let data = new FormData()
              data.append('account', this.account)
              data.append('password', this.password)
              data.append('email', this.email)

              let { data: result } = await axios.post('./singUp.php')
              if (result.status == 1) {
                this.success = true
              } else {
                this.success = false
              }
            }

            form.classList.add('was-validated')
          },
          false
        )
      })
    })()
  },
}
</script>
